import _get from 'lodash/get';

const uniqueId = (() => {
  let temp = 0;
  return () => temp++;
})();

class DaoAutocompleteController {
  constructor($scope) {
    'ngInject';

    this.$scope = $scope;
    this._get = _get;
    this.activate();
  }

  activate() {
    this.search = this.ngModel || '';
    this.isopen = false;
    this.error = false;
    this.errorMsg = ' ';
    this.id = `dac-dropdown-ul-${uniqueId()}`;
    // showCreate 
    this.showCreate = false;

    this.cutOptionsExpr();
    this.changeIndex();
  }

  myGet(val, str) {
    if (typeof(val) === 'string') {
      return val;
    } else if (typeof(val) === 'object') {
      return this._get(val, str);
    }
  }

  // 
  onMousedown(e) {
    //  stopPropagation  input  onBlur 
    e.stopPropagation();
    e.preventDefault();
    this.assignByIndex();
    this.close();
  }

  //  onFocus  dropdown 
  //  input  dropdown outsideClick dropdown 
  // 
  onInputClick(e) {
    e.stopPropagation();
  }

  onKeydown(e) {
    const ele = document.getElementById(this.id);
    switch (e.key) {
      case 'Enter':
        this.assignByIndex();
        this.close();
        break;
      case 'ArrowDown':
        e.preventDefault();
        if (this.index < this.filteredOptions.length - 1) {
          this.index++;
        }
        // 30  li 5  dropdown  5 li
        if (30 * this.index >= ele.scrollTop + 5 * 30) {
          ele.scrollTop += 30;
        }
        break;
      case 'ArrowUp':
        e.preventDefault();
        if (this.index > this.minIndex) {
          this.index--;
        }
        if (30 * this.index < ele.scrollTop) {
          ele.scrollTop -= 30;
        }
        break;
      default:
    }
  }

  onChange() {
    this.getFilteredOptions();
    this.toggleCreate();
    this.changeIndex();
    if (this.filteredOptions.length > 0) {
      this.error = false;
    }
    // hot-loadmodel
    if (this.hotLoad) {
      this.assign();
    }
    //  select 
    if (this.search.length > 0 || this.selectLike) {
      this.open();
    } else if (!this.selectLike && this.search.length === 0) {
      this.close();
    }
  }

  onMouseover(val) {
    this.index = val;
  }

  onMouseleave() {
    this.index = this.minIndex;
  }

  // onFocus  onClick  input 
  onFocus() {
    if (this.ngFocus) {
      this.ngFocus();
    }
    this.getFilteredOptions();
    if (this.selectLike) {
      this.open();
    }
  }

  onBlur() {
    if (this.ngBlur) {
      this.ngBlur();
    }
    this.assign();
    this.close();
  }

  open() {
    this.isopen = true;
  }

  close() {
    const ele = document.getElementById(this.id);
    ele.scrollTop = 0;
    this.isopen = false;
    this.index = this.minIndex;
  }

  cutOptionsExpr() {
    //  options 
    this.itemParts = this.optionsExpr.split(' in ');
    // itemName  xx in vm.xxx  xx
    this.itemName = this.itemParts[0];
    // itemOptions  xx in vm.xxx  vm.xx
    this.itemOptions = this.itemParts[1];
    // itemKey  xx.name  name
    this.itemKey = this.valueExpr.replace(`${this.itemName}.`, '');
  }

  getOptions() {
    //  options 
    this.options = this.$scope.$parent.$eval(this.itemOptions);
    return this.options;
  }

  filter(options) {
    const that = this;
    return options.filter(val => {
      return this.myGet(val, that.itemKey).toLowerCase().indexOf(that.search.toLowerCase()) > -1;
    });
  }

  getFilteredOptions() {
    this.filteredOptions = this.filter(this.options);
    return this.filteredOptions;
  }

  assign() {
    this.ngModel = this.search;
  }

  assignByIndex() {
    //  index  index 
    if (this.isopen && this.index >= 0 && this.filteredOptions.length > 0) {
      this.search = this.myGet(this.filteredOptions[this.index], this.itemKey);
    } else if (this.emptyPlaceholder) {
      this.error = true;
    }
    this.assign();
  }

  changeIndex() {
    if (!this.createPlaceholder) {
      //  0
      this.minIndex = 0;
    } else if (this.showCreate) {
      //  showCreate  true -1
      this.minIndex = -1;
    } else {
      //  showCreate  false 0
      this.minIndex = 0;
    }

    this.index = this.minIndex;
  }

  // 
  toggleCreate() {
    if (this.createPlaceholder) {
      if (!this.search) {
        //  search 
        this.showCreate = false;
      } else if (this.filteredOptions.length === 1) {
        //  search 
        this.showCreate = this.search !== this.myGet(this.filteredOptions[0], this.itemKey);
      } else {
        this.showCreate = true;
      }
    }
  }
}

export default DaoAutocompleteController;



